<template>
  <div id="home">
    <nav-bar nav-bg="#ff8e96">
      <div slot="center">购物街</div>
    </nav-bar>

    <!--当达到滚动高度的时候，让真实的tab栏隐藏，替换成下面的！！-->
    <tab-control :titles="titles" base-color="#ff8198"
                 v-on:tabItemClick="tabItemClick"
                 ref="tabControl1" v-show="isTabFixed" class="tab-control"/>

    <scroll class="content" ref="scroll" :probe-type="3"
            @scroll="contentScroll"
            @pullMore="pullMore"
            :pull-up-load="true">
      <home-swiper v-bind:banners="banners" @swiperImgLoad="swiperImgLoad"/>
      <recommend-view :recommends="recommends"/>
      <feature-view/>

      <!--商品展示列表-->
      <tab-control :titles="titles" base-color="#ff8198"
                   v-on:tabItemClick="tabItemClick"
                   ref="tabControl2" v-show="!isTabFixed"/>
      <goods-view :goods="currentData"/>
      <div v-if="isLoading" class="loading">{{ loadingTip }}</div>
    </scroll>

    <!-- @click.native修饰符，可以获取自定义组件的事件-->
    <back-top @click.native="backClick" v-show="isShowBackTop" ref="bckTop"/>

  </div>
</template>

<script>
// 导入的类型分好
import HomeSwiper from "@/views/home/childComps/HomeSwiper";
import RecommendView from "@/views/home/childComps/RecommendView";
import FeatureView from "@/views/home/childComps/FeatureView";

import NavBar from "@/components/common/navbar/NavBar";
import Scroll from "@/components/common/scroll/Scroll";
import TabControl from "@/components/content/tabControl/TabControl";
import GoodsView from "@/components/content/goods/GoodsView";
import {getHomeMultidata, getHomeGoods} from "@/network/home";

import {debounce} from "@/common/utils";

//导入混入的方法
import {itemListenerMixin,backTopMixin} from "@/common/mixin";


export default {
  name: "Home",
  components: {
    HomeSwiper,
    RecommendView,
    FeatureView,

    NavBar,
    Scroll,
    TabControl,
    GoodsView,
  },
  mixins:[itemListenerMixin,backTopMixin],
  data() {
    return {
      banners: null,
      recommends: null,
      titles: ['流行', '新款', '精选'],
      //确定组织数据的形式，如何有效的存储数据。 ==>数据模型==>请求数据==>存放到数据模型中
      //先去服务器请求，然后整理到goods中
      goods: {
        'pop': {page: 0, list: []},
        'new': {page: 0, list: []},
        'sell': {page: 0, list: []},
      },
      currentType: 'pop',
      contentScrollY: 0,
      isLoading: false,
      loadingTip: '正在疯狂加载中...',
      tabOffsetTop: 0,
      isTabFixed: false,
      saveY: 0,//路由切换时，保存位置
      homeImgLoadListener:null
    }
  },
  computed: {
    currentData() {
      return this.goods[this.currentType].list
    }
  },
  methods: {
    getHomeMultidata() {
      getHomeMultidata().then(value => {
        this.banners = value.data.data.banner.list;
        this.recommends = value.data.data.recommend.list;
        //console.log('getHomeMultidata==>', value);
      }).catch(error => {
        console.error('getHomeMultidata==>', error);
      });
    },
    getHomeGoods(type) {
      const page = this.goods[type].page + 1;
      getHomeGoods(type, page).then(value => {
        if (value.data.list.length <= 0) {
          //this.loadingTip = '找不到更多数据...'
          this.$refs.scroll.finishPullUp();
          this.isLoading = false;
          return;
        }
        //将请求的数据，追添到good中，然后页数+1
        setTimeout(() => {
          console.log('getHomeGoods-', type, '==>', value);
          this.goods[type].list.push(...value.data.list)
          this.goods[type].page++;
          // 数据获取成功，隐藏数据获取图标
          this.isLoading = false;
        }, 1500)
        setTimeout(() => {
          this.$refs.scroll.finishPullUp();
        }, 3000)
      }, error => {
        console.log('getHomeGoods-', type, '==>', error);
        this.isLoading = false;
        this.$refs.scroll.finishPullUp();
      })
    },

    tabItemClick(index) {
      //console.log('$emit--tabItemClick', index);
      if (index === 0) {
        this.currentType = 'pop';
      } else if (index === 1) {
        this.currentType = 'new'
      } else {
        this.currentType = 'sell'
      }
      this.$refs.tabControl1.currentTabIndex = index;
      this.$refs.tabControl2.currentTabIndex = index;
    },
/*    backClick() { //在混入mixin文件中已经声明了
      console.log('backClick');
      console.log('this.$refs.scroll.message-->', this.$refs.scroll.message);
      console.log('this.$refs.scroll.scroll-->', this.$refs.scroll.scroll);
      //通过this.$refs.scroll获取的是组件对象，不建议直接操作里面的属性。
      // 不建议直接使用组件中的属性的方法
      this.$refs.scroll.scroll.scrollTo(0, 0, 500);
    },*/
    contentScroll(position) {
      //console.log(position);
      this.contentScrollY = position.y;
      this.isShowBackTop = this.contentScrollY < -1000;
      //一旦滚动的距离大于offsetTop，就改变样式
      //console.log('contentScrollY',this.contentScrollY);
      if (-this.contentScrollY >= this.tabOffsetTop) {
        this.isTabFixed = true;
        //console.log('吸顶！！')
      } else {
        this.isTabFixed = false;
        //console.log('取消吸顶！！')
      }
    },
    pullMore() {
      //console.log('加载更多2222');
      //只有当状态不是正在刷新的时候，才会重新获取数据，不会出现重复问题
      if (!this.isLoading) {
        // 去加载更多，显示正在加载组件
        this.isLoading = true;
        this.getHomeGoods(this.currentType);
      }
    },
    swiperImgLoad() {
      //当图片加载成功后，再去获取tanControl距离顶部的offsetTop值
      console.log('offsetTop', this.$refs.tabControl2.$el.offsetTop);
      this.tabOffsetTop = this.$refs.tabControl2.$el.offsetTop-44;
    }
  },
  created() {
    /*
    一般在系统级别的接口（即生命周期之类的函数）里面只表达做了什么，不写具体细节，
    这就是视频里的封装逻辑
    */
    // 获取数据是异步操作
    this.getHomeMultidata();

    //获取三个类型的商品数据
    this.getHomeGoods('pop');
    this.getHomeGoods('new');
    this.getHomeGoods('sell');

  },
  // 内容为空，通过浑入传入mounted方法
  mounted() {
    // 图片加载更多
    //const refresh = debounce(this.$refs.scroll.refreshScroll, 100)
    //通过事件总线eventBus方式，获取孙子组件发射的方法
    //this.homeImgLoadListener = () => {
      //this.$refs不要放在created中 还有querySelector的查询，都不能放在created中
      //this.$refs && this.$refs.scroll && this.$refs.scroll.refreshScroll();

      //此时会出现加载过多的问题，每加载出一张图片，就会调用一次refresh方法，重新计算
      // ==> 解决办法，就是采用防抖函数，延迟时间执行，当下次执行的时候，取消掉前一个的定时函数，
      //refresh();
    //};
    //this.$bus.$on('itemImgLoad', this.homeImgLoadListener)

  },
  // 在进行路由器切换的时候，使用 activated和deactivated进行保存状态
  activated() {
    this.$refs.scroll.refreshScroll();
    this.$refs.scroll.scrollTo(0,this.saveY,0)
    this.$refs.scroll.refreshScroll();
    console.log('this.saveY',this.saveY);
  },
  deactivated() {
    this.saveY = this.contentScrollY;
    console.log('this.contentScrollY',this.contentScrollY);
    // 取消事件的处理
    this.$bus.$off('itemImgLoad',this.homeImgLoadListener);
  }

}
</script>

<style scoped>
#home {
  /*padding-top: 44px;*/
  /*padding-bottom: 888px;*/
  height: 100vh;
}

.tab-control {
  /*tabcontrol不见是因为父元素有overflow:hidden，sticky失效
  吸顶效果最好是监听屏幕的滚动，不要用position:sticky*/
  /*position: sticky;*/
  /*top: 42px;*/
  position: relative;
  z-index: 9;
}

.content {
  height: calc(100% - 144px);
}

.loading {
  text-align: center;
  height: 22px;
  line-height: 22px;
}
</style>
