<template>
  <div class="good-item" :key="good.link" v-on:click="goodItemClick">
    <!--<a :href="good.link">-->
    <img v-lazy="good.img" alt="" @load="itemImgLoad">
    <p>{{ good.title }}</p>
    <div class="info">
      <span class="price">{{ good.price |price }}</span>
      <span class="star">{{ good.cfav }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "goods",
  data(){
    return{
        iid:'1mlmy68 '
    }
  },
  props: {
    good: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  filters: {
    price(price) {
      return '￥' + price.toFixed(2)
    }
  },
  methods: {
    itemImgLoad() {
      // 方式一 使用事件总线的方式！
      this.$bus.$emit('itemImgLoad');
      // 方式二 使用下面的方式，可以直接获取到爷爷组件，也就是scroll组件，直接使用里面的方法
      //this.$parent.$parent.refreshScroll();
      //console.log(this.$parent.$parent);
    },
    goodItemClick() {
      //console.log('goodItemClick');
      this.$router.push('/detail/'+this.iid)
    }
  },
  beforeDestroy() {
    // 一旦组件销毁,就取消发射事件
    this.$off('itemImgLoad')
  }
}
</script>

