<template>
  <Swiper>
    <swiper-item v-for="(item,index) in banners" :key="index">
      <a :href="item.link">
        <img :src="item.image" alt="" @load="swiperLoad">
      </a>
    </swiper-item>
  </Swiper>
</template>

<script>
import {Swiper, SwiperItem} from "@/components/common/swiper"

export default {
  name: "HomeSwiper",
  data() {
    return {
      isLoaded: false
    }
  },
  props: {
    banners: {
      //type: Array,
      default() {
        return [];
      },
      required: true
    }
  },
  components: {
    Swiper,
    SwiperItem,
  },
  methods: {
    swiperLoad() {
      if (!this.isLoaded) {
        this.$emit('swiperImgLoad')
        this.isLoaded = true;
      }
    }
  }
}
</script>

