import {request} from "@/network/request";

export function getHomeMultidata() {
  return request({
    url: '/home/multidata'
  })
}

import {pop1,pop2,pop3} from "@/network/homeData/popData";
import {new1,new2,new3} from "@/network/homeData/newData";
import {sell1,sell2,sell3} from "@/network/homeData/sellData";

function getData(type,page){
  if(type==='pop'){
    return getPopData(page);
  }else if(type === 'new'){
    return getNewData(page);
  }else if(type === 'sell'){
  return getSellData(page);
  }
}
//返回模拟的数据
function getPopData(page){
  return new Promise((resolve, reject) => {
    switch (page){
      case 1:{
        resolve(pop1);
        break;
      }
      case 2:{
        resolve(pop2);
        break;
      }
      case 3:{
        resolve(pop2);
        break;
      }
      default:
        resolve({data:{list:[]}});
        break
    }
  });
}
function getNewData(page) {
  return new Promise((resolve, reject) => {
    switch (page){
      case 1:{
        resolve(new1);
        break;
      }
      case 2:{
        resolve(new2);
        break;
      }
      case 3:{
        resolve(new3);
        break;
      }
      default:
        resolve({data:{list:[]}});
        break
    }
  });
}
function getSellData(page){
  return new Promise((resolve, reject) => {
    switch (page){
      case 1:{
        resolve(sell1);
        break;
      }
      case 2:{
        resolve(sell2);
        break;
      }
      case 3:{
        resolve(sell3);
        break;
      }
      default:
        resolve({data:{list:[]}});
        break
    }
  });
}


export function getHomeGoods(type, page) {
  // 没有数据接口
  //return request({
  //  url:'',
  //  type,
  //  page,
  //})

  //模拟返回假数据
  return getData(type,page);
}
