<template>
  <div class="tab-control">
    <div v-for="(item, index) in titles" class="tab-control-item"
         @click="tabItemClick(index)">
      <span
        :style="currentTabIndex ===index?currentStyle:{}">{{ item }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TabControl",
  data() {
    return {
      currentTabIndex: 0
    }
  },
  props: {
    titles: {
      type: Array,
      default() {
        return []
      }
    },
    baseColor: {
      type: String,
      default: '#ff8198'
    }
  },
  computed: {
    currentStyle() {
      return {color:this.baseColor,borderBottomColor:this.baseColor};
    }
  },
  methods:{
    tabItemClick(index){
      console.log('tabItemClick,index=>',index);
      //通过vuex 存储index
      //this.$store.commit('setTabControlIndex',index)
      //console.log(this.$store.state.tabControlIndex);
      this.currentTabIndex = index;
      //利用组件之间的事件传递 子组件向父组件传递数据
      this.$emit('tabItemClick',index)
    }
  }
}
</script>

<style scoped>
.tab-control {
  display: flex;
  padding: 10px 0 16px;
  background-color: #fff;
  z-index: 9;
}

.tab-control-item {
  text-align: center;
  flex: 1;
  height: 22px;
  line-height: 22px;
  border-bottom: #ff8198;
}

.tab-control-item span {
  padding: 6px 10px;
  font-size: 16px;
  border-bottom: 2px solid transparent;;
  transition: all .2s;
}


.tab-control-item span.current {
  color: #ff8198;
  border-bottom: 3px solid #ff8198;
}
</style>
